import React from 'react';
import { useStateValue } from './StateProvider';
import "./Checkout.css";
import CheckoutProduct from './CheckoutProduct';
import Subtotal from './Subtotal';

function Checkout() {
    const [{ basket }] = useStateValue();

    return (
        <div className="checkout">
            <div className="checkout_left">

                <img
                    className="checkout_ad"
                    src="https://images-eu.ssl-images-amazon.com/images/G/31/img18/Fresh/Sept20/SVD/gw_mobile_hero_1500x200.jpg"
                    alt=""
                />
                <img
                    className="checkout_ad"
                    src="https://images-eu.ssl-images-amazon.com/images/G/31/img18/Fresh/Sept20/SVD/SBI_SVD_1500x120.jpg"
                    alt=""
                />
                {basket?.length === 0 ? (
                    <div>
                        <h2>Your Shopping Basket is Empty</h2>
                        <hr />
                    </div>
                ) : (
                        <div>
                            <h2>Your Shopping Basket</h2>
                            <hr />

                            {/* list of all the checkout products */}
                            {basket.map(item => (
                                <CheckoutProduct
                                    id={item.id}
                                    title={item.title}
                                    image={item.image}
                                    price={item.price}
                                    rating={item.rating}
                                />
                            ))}
                        </div>
                    )
                }
            </div>
            {basket.length > 0 && (
                <div className="checkout_right" >
                    <Subtotal />
                </div>

            )}
        </div>
    )
}

export default Checkout
